        line-height: 22px; }
      .fa {
        margin-right: 5px; }
      a {
        text-decoration: underline;
        &:hover {
          text-decoration: none; } } }
    &__bottom {
      text-align: center;
      padding: 20px 0;
      a {
        color: #666666;
        background: #fff;
        text-align: center;
        border: 1px solid #cfcfcf;
        border-bottom-width: 2px;
        border-radius: 5px;
        height: 36px;
        line-height: 33px;
        font-size: 16px;
        display: block;
        font-weight: 600;
        width: 145px;
        display: inline-block;
        + a {
          margin-left: 40px; }
        &:hover {
          background: #cfcfcf;
          color: #666; } } } }
  &__popup {
    display: none;
    position: absolute;
    min-width: 588px;
    height: auto;
    right: -5px;
    top: -5px;
    background: $color-white;
    box-shadow: 0 3px 19px rgba($link-color,.3);
    z-index: 100;
    padding: 23px;
    font-size: 14px;
    &.is-active {
      display: block; }
    .btn {
      line-height: 47px;
      &:hover {
        color: $color-white; }
      &+.btn {
        margin-left: 20px; } }
    &-title {
      color: $link-color;
      margin-bottom: 20px; }
    p {
      white-space: nowrap;
      &>.span {
        display: inline-block;
        &+span {
          margin-left: 10px; } } }
    &-close {
      position: absolute;
      right: 10px;
      top: 10px;
      z-index: 10;
      cursor: pointer;
      svg {
        width: 24px;
        height: 24px;
        .path-stroke {
          stroke: $primary-color; } } } } }
